
















































import Vue from 'vue'

export default Vue.extend({
  components: {
  },

  props: {
  },


  data () {
    return {
      open: false,

      words: [],
      alias: "",
    }
  },

  methods: {
    newAccount : async function() {
      this.open = false
      await Vue.api.createAccount({alias: this.alias})
      this.$store.dispatch("loadAccounts")
    },
  }
})
