
































import Vue from "vue";
import WordsAutocomplete from "@/components/WordsAutocomplete";

export default Vue.extend({
  components: {
    WordsAutocomplete,
  },

  props: {},

  data() {
    return {
      open: false,

      words: [] as string[],
      addressAlias: "",
    };
  },

  methods: {
    newContact: async function () {
      this.open = false;
      await Vue.api.newContact(this.words, this.addressAlias);
      this.$store.dispatch("loadContacts")
    },
  },
});
